.BingoSquareIncomplete {
    width: 100%;
    height: 100%;
    display: grid;
    place-items: center;
    color: #fff;
  box-shadow:
      0 0 7px #fff,
      0 0 10px #fff,
      0 0 21px #fff,
      0 0 42px #35fcf2,
      0 0 82px #35fcf2;
}

.BingoSquareComplete {
    margin: 1em;
    font-family: 'Press Start 2P', cursive;
    font: #FFF;
    width: 100%;
    height: 100%;
    display: grid;
    place-items: center;
    background: rgb(238,174,202);
    background: linear-gradient(0deg, rgba(238,174,202,1) 0%, rgba(253,51,136,1) 66%);
}